import { isWeiXin } from "./wechatAut";

let { serverConfig, getData } = window;
let { ip, httpAddress, socketAddress, imgAddress, getHrefNew, getQueryString } = serverConfig;
window.paying = false;
class wechatBuy{
    constructor(){
    }
    // 
    async get_wechatPay_h5(data) {
        if(window.paying){
            return;
        }
        console.log('', data)
        console.log(isWeiXin())
        if (!isWeiXin()) {
            // alert("~!")
            $toast.show('~!', 1000)
            return false;
        }
        window.paying  = true;
        $loading.show('..')
        let ret = await getData({url:`${httpAddress}/get_wechatPay_h5`,post:data})
        console.log("!!", ret);
        
        if (ret && ret.errcode == 0) {
            
             if (typeof WeixinJSBridge == "undefined"){
                if( document.addEventListener ){

                    document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady, false);
                }else if (document.attachEvent){

                    document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady); 
                    document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady);
                }
             }else{
                return await this.onBridgeReady(ret.data);
             }

         
        } else {
            // alert(':' + JSON.stringify(ret))
            $toast.show(''+JSON.stringify(ret), 1000)
        }

        $loading.hide()
        window.paying = false;
        return false;


    }
    onBridgeReady(saveData) {
        $loading.hide()

        return new Promise((resolve,reject)=>{
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest', saveData,
                function (res) {
                    window.paying = false;

                    // ,res.err_msg    ok 
                    if (res.err_msg == "get_brand_wcpay_request:ok") {
                        $toast.show('', 1000)
                        resolve(true)
                    } else if (res.err_msg == 'get_brand_wcpay_request:cancel') {
                        $toast.show('', 1000)
                    }
                    else {
                        $toast.show(res.err_msg, 1000)
                    }
                    resolve(false);

                }
            );

        })
    }
}

export default new wechatBuy()
