let { serverConfig, getData,wx } = window;
let { ip, httpAddress, socketAddress, imgAddress, getHrefNew, getQueryString } = serverConfig;

class jssdk {
    constructor(){

    }
    async get_wechat_config(){

      let we_conf = await getData({url:`${httpAddress}/get_wechat_config`})
      if( we_conf && we_conf.errcode == 0 ){
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: we_conf.appId, // 
            timestamp: we_conf.timestamp, // 
            nonceStr: we_conf.noncestr, // 
            signature: we_conf.signature,// 
            jsApiList: [
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'onMenuShareQQ',
                //'chooseImage',
                //'uploadImage'
                // 'updateAppMessageShareData',
                // 'updateTimelineShareData',
            ], // JS
        });
        wx.ready(function(){
            console.log('')
            // configreadyconfigconfigreadyready
        });
        wx.error(function(res){
            console.log('')
            console.log(res)

            // configerrorconfigdebugresSPA
        
        });

      }
    }
}

export default new jssdk()